.film-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.is-hidden {
  display: none;
}

.modal {
  position: absolute;
  z-index: 9999999999999;
  width: 280px;
  max-width: 100%;
  flex-shrink: 0;
  flex-grow: 0;

  padding-top: 48px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 41px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  background-color: #ffff;

  @media screen and (min-width: 768px) {
    width: 618px;
    padding-top: 42px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 40px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
  }

  @media screen and (min-width: 1024px) {
    width: 882px;
    height: 568px;
    padding-top: 50px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 40px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
  }
}

.film-container {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.modal__closebtn {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  border: none;
  cursor: pointer;
  color: #000000;
  background-color: transparent;
  background-image: url('../images/svg/icon-close.svg');
  background-position: center;
  background-size: 60%;
  background-repeat: no-repeat;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.modal__img {
  width: 240px;
  height: 356.57px;
  border-radius: 5px;
  margin-bottom: 30px;

  @media screen and (min-width: 768px) {
    width: 264px;
    height: 373.55px;
    margin-bottom: 0px;
    margin-right: 32px;
  }

  @media screen and (min-width: 1024px) {
    width: 396px;
    height: 477.8px;
    margin-bottom: 0px;
    margin-right: 30px;
  }
}

.modal__title {
  margin-top: 0;
  margin-bottom: 20px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.15;
  color: #000000;

  @media screen and (min-width: 1024px) {
    font-size: 30px;
    line-height: 1.16;
  }
}

.modal__table {
  margin-bottom: 20px;
}

.modal__table--caption {
  padding-right: 40px;
  padding-bottom: 10px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: #8c8c8c;
}

.modal__table--data {
  padding-bottom: 10px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: #000000;
}

.last_data {
  padding-bottom: 0;
}

.modal__table--vote {
  display: inline-table;
  text-align: center;
  align-items: center;
  width: 36px;
  height: 16px;
  background: #ff6b01;
  border-radius: 5px;
  color: #ffffff;
}

.modal__table--votes {
  display: inline-table;
  text-align: center;
  align-items: center;
  width: 36px;
  height: 16px;
  background: #f7f7f7;
  border-radius: 5px;
}

.modal__text--title {
  margin-top: 0px;
  margin-bottom: 10px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: #000000;
}

.modal__text {
  margin-top: 0px;
  margin-bottom: 20px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
  color: #000000;
}

.modal__button {
  display: flex;
  @media screen and (min-width: 768px) {
    position: absolute;
    bottom: 40px;
    right: 45px;
  }
  @media screen and (min-width: 1024px) {
    right: 215px;
  }
}

.modal__button--watched {
  margin-top: 0;
  padding: 0;
  text-transform: uppercase;
  margin-right: 15px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #ffffff;
  width: 112.58px;
  height: 44px;
  background: #ff6b01;
  border: 1px solid #ff6b01;
  border-radius: 5px;
  border-color: #ff6b01;
  cursor: pointer;
  @media screen and (min-width: 768px) {
    width: 125px;
  }

  @media screen and (min-width: 1024px) {
    width: 136px;
  }
}

.modal__button--queue {
  text-transform: uppercase;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #000000;
  background-color: #ffffff;
  width: 112.58px;
  height: 44px;
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 5px;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    width: 125px;
  }
  @media screen and (min-width: 1024px) {
    width: 136px;
  }
}
